@import url(https://fonts.googleapis.com/css?family=Muli:300,600,700,800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  font-family: 'Muli', sans-serif;
  font-size: 14px;
  background: #f5f5f5;
  color: #404040;
}
img {
  max-width: 100%;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.container{
  max-width: 450px;
  margin: auto;
  min-height: 100vh;
  background: white;
}
input,textarea{
  width: 100%;
  -webkit-appearance: none;
  font-size: 14px;
  background: white;
  border-radius: 6px;
  padding: 12px 16px;
  border: 1px solid #e2e2e2;
}
input:focus ,textarea:focus{
  outline: none;
}
button{
  height: 46px;
  width: 100%;
  border-radius: 6px;
  font-size: 16px;
  font-weight: 600;
  background: #a97ae4;
  border:1px solid #a97ae4;
  box-shadow: none;
  color: white;
  font-size: 14px;
  cursor: pointer;
  -webkit-transition: all .3s;
  transition: all .3s;
}
button:hover{
  background: #8c5bca;
  border: 1px solid #8c5bca;
  -webkit-transition: all .3s;
  transition: all .3s;
}
button:focus{
  outline: none;
}
.m-b-small{
  margin-bottom: 16px;
}
.m-b-medium{
  margin-bottom: 32px;
}
